export default {
  data: function data() {
    return {};
  },
  components: {},
  mounted: function mounted() {
    var _this = this;

    localStorage.removeItem("c_apiUserToken");
    localStorage.removeItem("c_apiUserId");
    localStorage.removeItem("c_ourMallUserInfo");
    document.title = this.$route.meta.title;
    this.$showLoading();
    this.$apiCall("api.User.adminLogin", {
      id: this.$route.query.id,
      t: this.$route.query.t,
      code: this.$route.query.code
    }, function (r) {
      _this.$hideLoading();

      if (r.ErrorCode == 9999) {
        if (!localStorage.getItem("c_ourMallFirstLogin")) {
          localStorage.setItem("c_ourMallFirstLogin", 1);
        } else {
          localStorage.setItem("c_ourMallFirstLogin", 2);
        }

        localStorage.setItem("c_apiUserToken", r.Data.Results.apiUserToken);
        localStorage.setItem("c_apiUserId", r.Data.Results.id);
        localStorage.setItem("c_ourMallUserInfo", JSON.stringify(r.Data.Results));

        _this.$store.commit("setUserInfo", r.Data.Results);

        _this.$router.push({
          name: "dashboard"
        });
      } else {
        _this.$hideLoading();

        _this.$elementMessage(r.Message, 'error');
      }
    });
  },
  methods: {}
};